import { createRouter, createWebHistory } from "@ionic/vue-router";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { getFirestore, doc, getDoc } from "firebase/firestore";
import TabsPage from "../views/TabsPage.vue";
import RegisterPage from "../views/RegisterPage.vue";
import AdminPage from "../views/AdminPage.vue";
import GuruPage from "../views/GuruPage.vue";
import SiswaPage from "../views/SiswaPage.vue";
import PetugasPage from "../views/PetugasPage.vue";
import OrangTuaPage from "../views/OrangTuaPage.vue";

const routes = [
  {
    path: "/",
    redirect: "/page",
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/LoginPage.vue"),
  },
  {
    path: "/splash",
    name: "splash",
    component: () => import("@/views/SplashScreen.vue"),
  },
  {
    path: "/page/",
    component: TabsPage,
    children: [
      {
        path: "",
        redirect: "/page/home",
      },
      {
        path: "home",
        name: "home",
        meta: { requiresAuth: true },
        beforeEnter: (to, from, next) => {
          const auth = getAuth();
          const db = getFirestore();

          const checkUserRole = async (user) => {
            if (user) {
              const userDoc = await getDoc(doc(db, "users", user.uid));
              if (userDoc.exists()) {
                const userData = userDoc.data();
                const userRole = userData.role;

                // Pengalihan berdasarkan peran pengguna
                if (userRole === "admin") {
                  next({ name: "admin" });
                } else if (userRole === "guru") {
                  next({ name: "guru" });
                } else if (userRole === "petugas") {
                  next({ name: "guru" });
                } else if (userRole === "siswa") {
                  next({ name: "siswa" });
                } else {
                  next(); // Pengalihan default jika peran tidak dikenali
                }
              } else {
                next({ name: "login" }); // Pengguna tidak ditemukan di Firestore
              }
            } else {
              next({ name: "login" }); // Pengguna tidak terautentikasi
            }
          };

          onAuthStateChanged(auth, (user) => {
            if (user) {
              checkUserRole(user).catch((error) => {
                console.error(error);
                next({ name: "login" });
              });
            } else {
              next({ name: "login" });
            }
          });
        },
      },
      {
        path: "/siswa",
        name: "siswa",
        component: SiswaPage,
      },
      {
        path: "/admin",
        name: "admin",
        component: AdminPage,
      },
      {
        path: "/guru",
        name: "guru",
        component: GuruPage,
      },
      {
        path: "/petugas",
        name: "petugas",
        component: PetugasPage,
      },
      {
        path: "/orangtua",
        name: "orangtua",
        component: OrangTuaPage,
      },
      {
        path: "soalsoal/:menuId",
        name: "soalsoal",
        component: () => import("@/views/SoalSoal/SoalSoal.vue"),
      },
      // Halaman Admin
      {
        path: "admin/laporankehadiran",
        name: "laporankehadiranAdmin",
        component: () => import("@/views/admin/LaporanKehadiran.vue"),
      },
      {
        path: "admin/laporanizin",
        name: "laporanizinAdmin",
        component: () => import("@/views/admin/LaporanIzin.vue"),
      },
      {
        path: "admin/halamanpengguna",
        name: "halamanpenggunaAdmin",
        component: () => import("@/views/admin/HalamanPengguna.vue"),
      },
      {
        path: "admin/halamansetting",
        name: "halamansettingAdmin",
        component: () => import("@/views/admin/HalamanSetting.vue"),
      },
      {
        path: "admin/halamankelas",
        name: "halamankelas",
        component: () => import("@/views/admin/TambahKelas.vue"),
      },
      {
        path: "admin/halamanmatapelajaran",
        name: "halamanmatapelajaran",
        component: () => import("@/views/admin/TambahMatePelajaran.vue"),
      },

      // Halaman Guru
      {
        path: "guru/laporankehadiran",
        name: "laporankehadiranGuru",
        component: () => import("@/views/guru/LaporanKehadiran.vue"),
      },
      {
        path: "guru/laporanizin",
        name: "laporanizinGuru",
        component: () => import("@/views/guru/LaporanIzin.vue"),
      },
      {
        path: "guru/halamanpengguna",
        name: "halamanpenggunaGuru",
        component: () => import("@/views/guru/HalamanPengguna.vue"),
      },
      {
        path: "guru/halamansetting",
        name: "halamansettingGuru",
        component: () => import("@/views/guru/HalamanSetting.vue"),
      },
      {
        path: "guru/tambahsoal",
        name: "tambahsoalGuru",
        component: () => import("@/views/guru/TambahSoal.vue"),
      },
      {
        path: "guru/tambahmateri",
        name: "tambahmateriGuru",
        component: () => import("@/views/guru/TambahMateri.vue"),
      },
      {
        path: "guru/buatujian",
        name: "buatujianGuru",
        component: () => import("@/views/guru/JadwalUjian.vue"),
      },
      {
        path: "guru/lihathasilujianguru",
        name: "lihatHasilUjianGuru",
        component: () => import("@/views/guru/LihatNilai.vue"),
      },

      // Halaman Petugas
      {
        path: "petugas/laporankehadiran",
        name: "laporankehadiranPetugas",
        component: () => import("@/views/petugas/LaporanKehadiran.vue"),
      },
      {
        path: "petugas/laporanizin",
        name: "laporanizinPetugas",
        component: () => import("@/views/petugas/LaporanIzin.vue"),
      },
      {
        path: "petugas/halamanpengguna",
        name: "halamanpenggunaPetugas",
        component: () => import("@/views/petugas/HalamanPengguna.vue"),
      },
      {
        path: "petugas/halamansetting",
        name: "halamansettingPetugas",
        component: () => import("@/views/petugas/HalamanSetting.vue"),
      },

      // Halaman user
      {
        path: "user/laporankehadiran",
        name: "laporankehadiranUser",
        component: () => import("@/views/user/LaporanKehadiran.vue"),
      },
      {
        path: "user/laporanizin",
        name: "laporanizinUser",
        component: () => import("@/views/user/LaporanIzin.vue"),
      },
      {
        path: "user/halamanpengguna",
        name: "halamanpenggunaUser",
        component: () => import("@/views/user/HalamanPengguna.vue"),
      },
      {
        path: "user/halamansetting",
        name: "halamansettingUser",
        component: () => import("@/views/user/HalamanSetting.vue"),
      },
      {
        path: "user/halamanujian/:jadwalId",
        name: "halamanujianUser",
        component: () => import("@/views/user/HalamanUjian.vue"),
      },
      {
        path: "user/jadwalujian",
        name: "jadwalujianUser",
        component: () => import("@/views/user/JadwalUjian.vue"),
      },
      {
        path: "user/lihathasilujian",
        name: "lihathasilujianUser",
        component: () => import("@/views/user/LihatHasilUjian.vue"),
      },
      {
        path: "user/lihatmaterimateri",
        name: "lihatmaterimateri",
        component: () => import("@/views/user/HalamanMateri.vue"),
      },
    ],
  },
  {
    path: "/register",
    name: "register",
    component: RegisterPage,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
