const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const checkGeolocation = async () => {
  return new Promise((resolve) => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const userLat = position.coords.latitude;
          const userLng = position.coords.longitude;
          const distance = calculateDistance(
            userLat,
            userLng,
            allowedCoordinates.value.latitude,
            allowedCoordinates.value.longitude
          );
          const allowedRadius = 0.1; // 100 meters
          resolve(distance <= allowedRadius);
        },
        (error) => {
          console.error("Error getting geolocation:", error);
          resolve(false);
        }
      );
    } else {
      console.error("Geolocation is not supported by this browser.");
      resolve(false);
    }
  });
};

const calculateDistance = (lat1, lng1, lat2, lng2) => {
  const toRad = (value) => (value * Math.PI) / 180;
  const R = 6371; // Radius of the Earth in km
  const dLat = toRad(lat2 - lat1);
  const dLng = toRad(lng2 - lng1);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRad(lat1)) *
      Math.cos(toRad(lat2)) *
      Math.sin(dLng / 2) *
      Math.sin(dLng / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c;
  return distance;
};

const isLate = (timestamp) => {
  if (!timestamp) return false;
  const time = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
  if (isNaN(time.getTime())) return false;
  const eightAM = new Date(time);
  eightAM.setHours(8, 0, 0, 0); // 8 AM
  return time > eightAM;
};

const calculateLateness = (timestamp) => {
  if (!timestamp) return "0 menit";
  const time = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
  if (isNaN(time.getTime())) return "0 menit";
  const eightAM = new Date(time);
  eightAM.setHours(8, 0, 0, 0); // 8 AM
  const diff = time - eightAM; // Difference in milliseconds
  const hours = Math.floor(diff / (1000 * 60 * 60));
  const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
  return `${hours} jam ${minutes} menit`;
};

const formatDayDate = (dateString) => {
  const date = new Date(dateString);
  const days = ["Minggu", "Senin", "Selasa", "Rabu", "Kamis", "Jumat", "Sabtu"];
  const day = days[date.getDay()];
  const formattedDate = date.toLocaleDateString("id-ID", {
    day: "numeric",
    month: "long",
    year: "numeric",
  });
  return `${day}, ${formattedDate}`;
};

onMounted(() => {
  onAuthStateChanged(auth, (user) => {
    if (user) {
      fetchUserName(user.uid);
      fetchCoordinates(); // Fetch coordinates from Firestore
      fetchAttendanceRecords(user.uid);
      checkAbsenStatus(user.uid); // Check absen status on mount
    } else {
      router.push({ name: "login" });
    }
  });
});

const toNavigate = (routeName) => {
  if (userStatus.value === "non-aktif") {
    showToast(
      "Akun Anda belum aktif. Silakan hubungi admin untuk mengaktifkan.",
      "danger"
    );
    return;
  }
  router.push({ name: routeName });
  console.log(`Navigating to menu ${routeName}`);
};
